import React, { useState, useEffect } from "react";
import { Container } from "./Gallery.style";
import CircleLoader from "../../components/CircleLoader/CircleLoader";
import { LoaderWrapper } from "../../common/style";
import { Link } from "react-router-dom";
import { getAllProducts } from "./../../api/products";
const GalleryProduct = React.lazy(() => import("./GalleryProduct"));

const Gallery = () => {
  const [list, setList] = useState([]);
  useEffect(() => {
    async function fetchData() {
      const res = await getAllProducts();
      console.log("res", res);

      setList(res);
    }
    fetchData();
  }, []);

  console.log("gallery render");
  return (
    <Container>
      {list.length < 1 ? (
        <LoaderWrapper>
          <CircleLoader />
        </LoaderWrapper>
      ) : (
        list?.map((p) => {
          const isAvailible = Number(p.qtt) > 0;
          return (
            <Link to={`/gallery/${p.id}`} className="product" key={`${p.id}`}>
              <GalleryProduct
                id={p.id}
                title={p.title}
                src={p.src}
                details={p.details}
                price={p.price}
                isAvailible={isAvailible}
                qtt = {p.qtt}
              />
            </Link>
          );
        })
      )}
    </Container>
  );
};

export default Gallery;
