import styled, { css } from "styled-components";
import { disableStyle } from "../../common/style";
export const Container = styled.div`
  min-height: 704px;
  .product {
    text-decoration: none;
  }
  width: 100%;
  border: solid var(--borderColor) 1px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 980px;
`;
export const ProductBlock = styled.div`
  margin: 0.5rem;
  padding: 1.5rem;
  text-align: left;
  color: var(--text_color);
  text-decoration: none;
  border: 1px solid var(--borderColor);
  border-radius: 10px;
  transition: color 0.15s ease, border-color 0.15s ease;
  width: 300px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  position: relative;

  :hover,
  :focus,
  :active {
    border-color: var(--linkColor);
    background: none;
  }
  h2 {
    margin: 0 0 1rem 0;
    font-size: 1.5rem;
    text-align: center;
  }
  p {
    margin: 0;
    font-size: 1.25rem;
    line-height: 1.5;
  }
  img {
    width: 150px;
    height: auto;
    align-self: center;
  }
  ${({ isAvailible }) =>
    !isAvailible &&
    css`
      ${disableStyle}
      cursor: pointer;
    `}

  .notAvailibleSticker {
    position: absolute;
    top: 50%;
    left: 50%;
    display:flex ;
    transform: translate(-50%, -50%);
    width: 70%;
    border-radius:5000px;
    justify-content:center ;
    align-items:center ;
    height: 20%;
    padding:10px ;
    background:rgba(51, 170, 51, .4);
    opacity:1;
    color:red;
    text-align:center ;
    font-weight:bold ;
    user-select:none;
  }
`;
